export default {
	props: {
		resdata: Array,
		height:{
			type:Number,
			default:350
		},
		preview:{
			type:Boolean,
			default:false
		}
	},
	computed:{
		getStyle() {
			return `height: ${this.height}rpx`
		},
		getUrls() {
			return this.resdata.map( v => v)
		}
	},
	methods: {
		event(item,index){
			if(this.preview){
				return uni.previewImage({
					current:index,
					urls:this.getUrls,
					indicator:"default"
				})
			}
		}
	}
}
